.icon-menu {
    display:block;
    position:absolute;
    width:auto;
    margin:40px 0 0 -43px;
    padding:20px;
    border:1px solid #ccc;
    border-radius:3px;
    background: #fff;
    overflow:hidden;
    clear:both;
}
.icon-container {
    height:100%;
    overflow-y:scroll;
    clear:both;
}
.icon {
    float:left;
    margin:2px;
    padding:3px 4px;
    border-radius:3px;
    cursor:pointer;
}
.icon:hover {
    background:#ccc;
}
.icon-filter-box {
    width: 300px;
}
